module.exports = {"posts":[{"path":"posts/migrating-to-webpack2.md","fileName":"migrating-to-webpack2","title":" Migrating to Webpack 2","date":1483934498000,"content":{"title":" Migrating to Webpack 2","children":[{"type":"heading","children":[{"type":"text","content":" Migrating to Webpack 2"}],"h":"1"},{"type":"paragraph","children":[{"type":"text","content":" These days everybody is talking about "},{"type":"em","children":[{"type":"text","content":" tree shaking"}]},{"type":"text","content":"  for optimizing bundle size of "},{"type":"em","children":[{"type":"text","content":" Single Page Applications"}]},{"type":"text","content":"  (SPA)."}]},{"type":"paragraph","children":[{"type":"text","content":" There are there options, as far as Im aware of, to achieve such  thing:"}]},{"type":"bullet_list","children":[{"type":"list_item","children":[{"type":"paragraph","children":[{"type":"strong","children":[{"type":"text","content":" Rollup"}]},{"type":"text","content":"  is another packager for JavaScript applications."}]}]},{"type":"list_item","children":[{"type":"paragraph","children":[{"type":"strong","children":[{"type":"text","content":" Google Closure Compiler"}]},{"type":"text","content":"  which can also generate nice human readable code."}]}]},{"type":"list_item","children":[{"type":"paragraph","children":[{"type":"strong","children":[{"type":"text","content":" Webpack 2"}]},{"type":"text","content":"  which is the one Im going to try because I really like "},{"type":"strong","children":[{"type":"text","content":" Webpack"}]},{"type":"text","content":"  and I already have some loaders for it."}]}]}]},{"type":"paragraph","children":[{"type":"strong","children":[{"type":"text","content":" Webpack"}]},{"type":"text","content":"  "},{"type":"em","children":[{"type":"text","content":" minification"}]},{"type":"text","content":"  relays on "},{"type":"strong","children":[{"type":"text","content":" UglfyJS"}]},{"type":"text","content":"  which has some issues with "},{"type":"em","children":[{"type":"text","content":" immediately-invoked function expression"}]},{"type":"text","content":"  (IIFE) classes generated by "},{"type":"em","children":[{"type":"text","content":" transpilers"}]},{"type":"text","content":". Im not an expert on the subject, but I think it may be solved once we can use "},{"type":"strong","children":[{"type":"text","content":" UglifyJS"}]},{"type":"text","content":"  directly with "},{"type":"strong","children":[{"type":"text","content":" ES6"}]},{"type":"text","content":". In any case, I can live with this small issue."}]},{"type":"paragraph","children":[{"type":"text","content":" As a reference, with "},{"type":"strong","children":[{"type":"text","content":" Webpack 1"}]},{"type":"text","content":"  Im getting this numbers on my bundles:"}]},{"type":"fence","content":"469K -> bundle.js\n3.8M -> bundle.js.map\n","lang":""},{"type":"heading","children":[{"type":"text","content":" First step, verify that everything is running as it should"}],"h":"2"},{"type":"paragraph","children":[{"type":"text","content":" Done."}]},{"type":"heading","children":[{"type":"text","content":" Update to Webpack 2"}],"h":"2"},{"type":"paragraph","children":[{"type":"text","content":" The latest version of "},{"type":"strong","children":[{"type":"text","content":" Webpack"}]},{"type":"text","content":"  at the moment of writing this post is "},{"type":"code_inline","content":"2.2.0-rc.3"},{"type":"text","content":"  which is the one Im getting now. It will need also an upgrade of "},{"type":"em","children":[{"type":"text","content":" webpack-dev-server"}]},{"type":"text","content":"  to "},{"type":"code_inline","content":"2.2.0-rc.0"},{"type":"text","content":"."}]},{"type":"paragraph","children":[{"type":"text","content":" After changing the version in my "},{"type":"code_inline","content":"package.json"},{"type":"text","content":"  and running "},{"type":"code_inline","content":"npm install"},{"type":"text","content":"  Im getting some errors when I try to run "},{"type":"code_inline","content":"npm start"},{"type":"text","content":"."}]},{"type":"fence","content":"throw new WebpackOptionsValidationError(webpackOptionsValidationErrors);\n","lang":""},{"type":"paragraph","children":[{"type":"text","content":" No surprises at this point. What was a surprise was that my bundle was almost the same size as before."}]},{"type":"paragraph","children":[{"type":"text","content":" A handy tool for this kind of scenarios is the "},{"type":"link","children":[{"type":"text","content":" Webpack Visualizer"}],"attrs":{"href":"https://chrisbateman.github.io/webpack-visualizer/"}},{"type":"text","content":"  from "},{"type":"em","children":[{"type":"text","content":" chrisbateman"}]},{"type":"text","content":". After running it on my build, I noticed that a third part of it was "},{"type":"strong","children":[{"type":"text","content":" RxJS"}]},{"type":"text","content":", which has like a hundred methods in its API from where I usually use less than ten."}]},{"type":"paragraph","children":[{"type":"text","content":" The problem with "},{"type":"strong","children":[{"type":"text","content":" RxJS"}]},{"type":"text","content":"  is that "},{"type":"strong","children":[{"type":"text","content":" UglifyJS"}]},{"type":"text","content":"  will not be a good improvement because many of the operators are added to the "},{"type":"em","children":[{"type":"text","content":" Observable"}]},{"type":"text","content":"  prototype with "},{"type":"em","children":[{"type":"text","content":" IIFE"}]},{"type":"text","content":"  functions that are considered "},{"type":"em","children":[{"type":"text","content":" impure"}]},{"type":"text","content":"  (which they are) and hence not removed even if the operator itself is not used. The solution for this was to "},{"type":"em","children":[{"type":"text","content":" import only what you need"}]},{"type":"text","content":"  which is the recommended approach from "},{"type":"strong","children":[{"type":"text","content":" RxJS"}]},{"type":"text","content":"  documentation."}]},{"type":"fence","content":"// Before\nimport {Observable, Subject} from 'rxjs'\n\n// After\nimport {Observable} from 'rxjs/Observable'\nimport {Subject} from 'rxjs/Subject'\nimport 'rxjs/add/operator/merge'\nimport 'rxjs/add/operator/filter'\nimport 'rxjs/add/operator/map'\n\n","lang":"javascript"},{"type":"paragraph","children":[{"type":"text","content":" The final numbers are:"}]},{"type":"fence","content":"301K -> bundle.js\n2.3M -> bundle.js.map\n","lang":""}]},"modifiedAt":1483934498000},{"path":"posts/a-nodejs-proxy.md","fileName":"a-nodejs-proxy","title":" A NodeJS Proxy with no Dependencies","date":1477890795000,"content":{"title":" A NodeJS Proxy with no Dependencies","children":[{"type":"heading","children":[{"type":"text","content":" A NodeJS Proxy with no Dependencies"}],"h":"1"},{"type":"paragraph","children":[{"type":"text","content":" I recently needed to write a "},{"type":"em","children":[{"type":"text","content":" proxy"}]},{"type":"text","content":"  using Node. Ive done this before using the "},{"type":"code_inline","content":"http-proxy"},{"type":"text","content":"  library"},{"type":"text","content":" which is pretty good. But on the new"},{"type":"text","content":" code I wanted to prevent the unnecessary proliferation of dependencies. My option was then to study a bit of"},{"type":"text","content":" how Node handles "},{"type":"em","children":[{"type":"text","content":" requests"}]},{"type":"text","content":", "},{"type":"em","children":[{"type":"text","content":" responses"}]},{"type":"text","content":"  and streams."}]},{"type":"paragraph","children":[{"type":"text","content":" At the end, it resulted quite simple:"}]},{"type":"fence","content":"// Create your server as you normally do\nconst server = http.createServer((req, res) => {\n    // Prepare your new request options,\n    // basically copying from the original request.\n    const opts = {\n        hostname: endpoint, // The endpoint you want to proxy to\n        port: endpointPort, // Usually 80\n        path: req.url,\n        method: req.method,\n        headers: req.headers\n    };\n        \n    const proxyRequest = http.request(opts, proxyResponse => {\n        proxyResponse.on('data', chunk => { res.write(chunk, 'binary'); });\n        proxyResponse.on('end', () => { res.end(); });\n        proxyResponse.on('error', er => {\n            throw new Error('Error processing response: ' + er.message);\n        });\n        res.writeHead(proxyResponse.statusCode, proxyResponse.headers);\n    });\n    req.on('data', chunk => { proxyRequest.write(chunk, 'binary'); });\n    req.on('end', () => { proxyRequest.end(); });\n});\nserver.listen(port); // Whatever port you decide to use\n","lang":"javascript"},{"type":"paragraph","children":[{"type":"text","content":" And thats pretty much all."}]},{"type":"paragraph","children":[{"type":"text","content":" In my mind ~20 lines of code are better than adding dependencies into my project."}]}]},"modifiedAt":1477891887000},{"path":"posts/writing-my-own-syntax-coloring-tool.md","fileName":"writing-my-own-syntax-coloring-tool","title":" Writing My own Syntax Coloring Tool","date":1476671116000,"content":{"title":" Writing My own Syntax Coloring Tool","children":[{"type":"heading","children":[{"type":"text","content":" Writing My own Syntax Coloring Tool"}],"h":"1"},{"type":"paragraph","children":[{"type":"text","content":" The problem is quite simple, I want to have colored syntax on the code"},{"type":"text","content":" I paste into my posts."}]},{"type":"paragraph","children":[{"type":"text","content":" Why not use one of those plugins around the net?"}]},{"type":"paragraph","children":[{"type":"text","content":" The first reason is that they usually work directly on DOM elements or HTML strings."},{"type":"text","content":" You tell them where is you "},{"type":"em","children":[{"type":"text","content":" code"}]},{"type":"text","content":"  and what "},{"type":"em","children":[{"type":"text","content":" language"}]},{"type":"text","content":"  you use and they"},{"type":"text","content":" format it replacing your original HTML with another one full of "},{"type":"code_inline","content":"spans"},{"type":"text","content":" that can be colored with regular CSS."}]},{"type":"paragraph","children":[{"type":"text","content":" The problem I have is that I dont want to generate HTML as Im using React who"},{"type":"text","content":" is already generated the DOM elements. I want something that I can use in React"},{"type":"text","content":" in the most natural possible way."}]},{"type":"paragraph","children":[{"type":"text","content":" After looking a bit at the options, I decided to use this as a justification for an exercise"},{"type":"text","content":" on writing a very naive and simple Lexer."}]},{"type":"paragraph","children":[{"type":"text","content":" I'll start with JavaScript, which is the language I write more often:"}]},{"type":"paragraph","children":[{"type":"text","content":" This is a simple example of JS in a "},{"type":"code_inline","content":"tab"},{"type":"text","content":"  delimited code block:"}]},{"type":"code_block","content":"const f = x => x;\n"},{"type":"paragraph","children":[{"type":"text","content":" And here is the fenced version of it:"}]},{"type":"fence","content":"const f = x => x;\n","lang":"javascript"},{"type":"paragraph","children":[{"type":"text","content":" The first thing I need is some sort of grammar definition,"},{"type":"text","content":" my custom solution looks pretty much as other common implementations:"}]},{"type":"fence","content":"const grammar = {\n    space: /^\\s+/,\n    word: /^[^\\s]+/\n};\n","lang":"javascript"},{"type":"paragraph","children":[{"type":"text","content":" Where each key represents a "},{"type":"em","children":[{"type":"text","content":" type"}]},{"type":"text","content":"  of token and the value is the RegEx"},{"type":"text","content":" to find them."}]},{"type":"paragraph","children":[{"type":"text","content":" Using this grammar to parse "},{"type":"code_inline","content":"\"Hello World\""},{"type":"text","content":"  you should get something like:"}]},{"type":"fence","content":"[\n    { \"type\": \"word\", \"value\": \"Hello\" },\n    { \"type\": \"space\", \"value\": \" \" },\n    { \"type\": \"word\", \"value\": \"Wolrd\" },\n]\n","lang":"javascript"},{"type":"paragraph","children":[{"type":"text","content":" There are really clever ways to do this, but in a naive implementation,"},{"type":"text","content":" you can write a function that looks at the start of a line, look for a match"},{"type":"text","content":" on the set of rules you have, add the found token to your array of tokens"},{"type":"text","content":" and remove the token substring from the original input:"}]},{"type":"fence","content":"function Lexer (ruleDefs) {\n    const rules = new Rules(ruleDefs);\n\n    this.lex = input => {\n        let rest = input;\n        const tokens = [];\n        while (rest.length > 0) {\n            const r = rules.match(rest);\n            rest = r.rest;\n            tokens.push(r.token);\n        }\n        return tokens;\n    };\n}\n","lang":"javascript"},{"type":"paragraph","children":[{"type":"text","content":" Then, in "},{"type":"em","children":[{"type":"text","content":" React"}]},{"type":"text","content":", you can write something like:"}]},{"type":"fence","content":"const CodeFenced = (node: IFenced, index: string) => {\n    const tokens = jsLexer.lex(node.content);\n    const spans = tokens.map((t, idx) => <span className = {t.type} key = {idx}>{t.value}</span>);\n    return <pre key = {index} className = \"fenced\" >\n        { spans }\n    </pre>;\n};\n","lang":"javascript"},{"type":"paragraph","children":[{"type":"text","content":" And so far thats all. My complete solution for color syntax is about 80 lines of code including"},{"type":"text","content":" javascript syntax definition."}]}]},"modifiedAt":1476671116000},{"path":"posts/towards-the-end-of-gender-discussion.md","fileName":"towards-the-end-of-gender-discussion","title":" Towards the End of Gender Discussion","date":1473038104000,"content":{"title":" Towards the End of Gender Discussion","children":[{"type":"heading","children":[{"type":"text","content":" Towards the End of Gender Discussion"}],"h":"1"},{"type":"paragraph","children":[{"type":"text","content":" I just read "},{"type":"link","children":[{"type":"text","content":" Oregon Court Allows a Person to Choose Neither Sex"}],"attrs":{"href":"http://nyti.ms/1XRRG5D"}},{"type":"text","content":"  and was happily shock by it."}]},{"type":"paragraph","children":[{"type":"text","content":" Finally, a sentence that makes sense in the context of gender."}]},{"type":"paragraph","children":[{"type":"text","content":" Regardless of the funny and nerdy name for this new "},{"type":"em","children":[{"type":"text","content":" no-gender"}]},{"type":"text","content":"  gender (nonbinary), this is, I believe, the starting point of a more sound and modern path for society."}]},{"type":"paragraph","children":[{"type":"text","content":" For law and society we should not be women or men or gay or black or asian or latino or whatever, we should be only people, human beings, persons."}]},{"type":"paragraph","children":[{"type":"text","content":" Law and society should give a damn about what do we do with our sexuality, what color is our skin or what religion we like to practice."}]},{"type":"paragraph","children":[{"type":"text","content":" For now, and this is only for now because hopefully in the future science would change it, the only importance of gender is when we talk about reproduction. Unfortunately we still need at least one woman and at least one man to be involved in pregnancy. Reproduction is, in my point of view, the only field where we still need to talk about gender when talking about rules, enforced by law or by society."}]},{"type":"paragraph","children":[{"type":"text","content":" Marriage, for example, should be a contract that allow two human beings to establish a long term commitment to each other regardless of genders, race or religion."}]},{"type":"paragraph","children":[{"type":"text","content":" As for what restroom we should use well, that discussion should not even exists, telling people what restroom to use is like telling them what seat should they occupy in a bus based on the color of their skin. Having icons for men or women on restrooms is an aberration. People is people and people need to pee regardless of what kind of gametes their body produce. (If the concern is to reduce sexual harassment, the first thing we should realize is that it can happen regardless the gender of the aggressor or the victims. We should prevent it in any possible way but without discrimination)"}]},{"type":"paragraph","children":[{"type":"text","content":" I dont mean that we should not talk about gender at all. We can and we should talk about gender, as we do about religion, when we talk as individuals, when we talk about what do we think about our selves, how do we want to be looked at by others, etc. (Race is a different matter, I think race should not even be there for individual discourse, the color of our skin is just like our names or our hight, just another personal characteristic and nothing more, race and racial should be abolished from our vocabulary, they dont even make sense in biological terms)"}]},{"type":"paragraph","children":[{"type":"text","content":" Thats all I wanted to say and I didnt want to forget it so I write it down as soon as I wake up. Ill review it later and publish it."}]}]},"modifiedAt":1473038104000},{"path":"posts/fixing_and_updating_my_blog.md","fileName":"fixing_and_updating_my_blog","title":" Fixing and Updating My Blog","date":1472962259000,"content":{"title":" Fixing and Updating My Blog","children":[{"type":"heading","children":[{"type":"text","content":" Fixing and Updating My Blog"}],"h":"1"},{"type":"paragraph","children":[{"type":"text","content":" After some time I came back to this blog and when I wanted to push a new post, well, the thing doesnt work."}]},{"type":"paragraph","children":[{"type":"text","content":" So, the first problem: I upgraded my version of "},{"type":"em","children":[{"type":"text","content":" node"}]},{"type":"text","content":"  some time ago. Now I have version 6.5, who knows what version I had when this used to work about 6 months ago (is it not frustrating how fast your code becomes obsolete in "},{"type":"em","children":[{"type":"text","content":" Frontend"}]},{"type":"text","content":"  development?)."}]},{"type":"paragraph","children":[{"type":"text","content":" My first attempt is trying to make the full thing work again without changing too much my code."}]},{"type":"paragraph","children":[{"type":"text","content":" OK, so far, so good, I just needed to update my "},{"type":"em","children":[{"type":"text","content":" css-loader"}]},{"type":"text","content":"  and my "},{"type":"em","children":[{"type":"text","content":" stylus-loader"}]},{"type":"text","content":"  for "},{"type":"em","children":[{"type":"text","content":" Webpack"}]},{"type":"text","content":". I still have a bunch of old dependencies that I want to update. But now all works again and I can start adding posts again."}]},{"type":"paragraph","children":[{"type":"text","content":" The next step is to move and organize a bit all the "},{"type":"em","children":[{"type":"text","content":" Webpack"}]},{"type":"text","content":"  scripts. They are all around in the root directory. Im moving all of them to a dedicated folder."}]},{"type":"paragraph","children":[{"type":"text","content":" Now we need to upgrade "},{"type":"em","children":[{"type":"text","content":" Webpack"}]},{"type":"text","content":"  and its loaders. Just to avoid any other broken functionality due to old versions."}]},{"type":"paragraph","children":[{"type":"text","content":" Now, lets start with some more fun. The first thing, add "},{"type":"em","children":[{"type":"text","content":" TypeScript"}]},{"type":"text","content":"  support and remove "},{"type":"em","children":[{"type":"text","content":" Babel"}]},{"type":"text","content":"  for good. (I really like "},{"type":"em","children":[{"type":"text","content":" Babel"}]},{"type":"text","content":", but I prefer to use "},{"type":"em","children":[{"type":"text","content":" TypeScript"}]},{"type":"text","content":"  compiler directly)"}]},{"type":"paragraph","children":[{"type":"text","content":" Unfortunately, seems that using "},{"type":"em","children":[{"type":"text","content":" TypeScript"}]},{"type":"text","content":"  is no that easy, I got into the problem of updating almost all my dependencies, finding typing definitions for a bunch of things and of course now I have another problem, I also need to upgrade my components to use the new React way."}]},{"type":"paragraph","children":[{"type":"text","content":" OK, seems that all is working again. "},{"type":"code_inline","content":"npm outdated"},{"type":"text","content":"  is not giving me any warnings. At least for the next minute, Im up to date!"}]},{"type":"paragraph","children":[{"type":"text","content":" After dealing with all these small issues Im more and more tempted to start using a different approach like "},{"type":"em","children":[{"type":"text","content":" ELM"}]},{"type":"text","content":"  or "},{"type":"em","children":[{"type":"text","content":" PureScript"}]},{"type":"text","content":". Im just sort of tired of the JavaScript ecosystem."}]},{"type":"paragraph","children":[{"type":"em","children":[{"type":"text","content":" Update Sep-15"}]},{"type":"text","content":"  I have removed all need for doing AJAX calls. Also removed Highlands, I was using it for doing streaming in Node, instead Im using RxJS also in the build process."}]},{"type":"paragraph","children":[{"type":"em","children":[{"type":"text","content":" Update Sep-16"}]},{"type":"text","content":"  I have removed React Router. Not a huge improvement on the bundle size, but at least I have less dependencies to worry about."}]},{"type":"paragraph","children":[{"type":"em","children":[{"type":"text","content":" Update Sep-18"}]},{"type":"text","content":"  I was able to remove "},{"type":"em","children":[{"type":"text","content":" Markdown-it"}]},{"type":"text","content":"  from the bundled dependencies. Im not using "},{"type":"em","children":[{"type":"text","content":" Commonmark.js"}]},{"type":"text","content":"  as was my original intention because Im planning to add some custom logic on the parsing and right ow "},{"type":"em","children":[{"type":"text","content":" Commonmark.js"}]},{"type":"text","content":"  doesnt support custom logic on its parser."}]},{"type":"paragraph","children":[{"type":"em","children":[{"type":"text","content":" Update Oct-02"}]},{"type":"text","content":"  Created the Archive section.  Post count on home page is limited to 5."}]},{"type":"paragraph","children":[{"type":"em","children":[{"type":"text","content":" Update Oct-16"}]},{"type":"text","content":"  Using Modern Latin 10pts. Also revisited all the styles in the blog and added some color"},{"type":"text","content":" syntax for fenced code with a very naive and simplistic lexer."}]},{"type":"paragraph","children":[{"type":"text","content":" My TODO now is:"}]},{"type":"bullet_list","children":[{"type":"list_item","children":[{"type":"paragraph","children":[{"type":"text","content":" Add MathJax support for writing some math on my posts."}]}]}]}]},"modifiedAt":1476671116000},{"path":"posts/moving-to-react.md","fileName":"moving-to-react","title":" Moving to React and Webpack","date":1449461570000,"content":{"title":" Moving to React and Webpack","children":[{"type":"heading","children":[{"type":"text","content":" Moving to React and Webpack"}],"h":"1"},{"type":"paragraph","children":[{"type":"text","content":" Well, the first thing to say is why. I dont update this blog so often, why dont keep it just like it is? Because I started it as a way to learn, more than a real publishing platform and now I want to have some practice on React (I use Angular at my job)."}]},{"type":"paragraph","children":[{"type":"text","content":" I like Angular, but React seems to me a more natural approach for building apps. All the idea about directives, controllers and services is good, but not very functional if you think about it. I mean, functional in the terms of functional programming."}]},{"type":"paragraph","children":[{"type":"text","content":" React combined with Reactive Extensions seems a more natural environment for functional programming."}]},{"type":"paragraph","children":[{"type":"text","content":" Other reasons are:"}]},{"type":"bullet_list","children":[{"type":"list_item","children":[{"type":"paragraph","children":[{"type":"text","content":" I want to use Webpack and its hot loading, ES6 and TypeScript support, as well as its packaging capabilities."}]}]},{"type":"list_item","children":[{"type":"paragraph","children":[{"type":"text","content":" Configuring Webpack with Angular 1.x is a bit tricky and full of magic. I have a very bad memory and always forget what is happening behind all this magic tools with magic words as \"ng-inject\" in the middle of the file. This makes me a bit uncomfortable with Angular/Webpack."}]}]},{"type":"list_item","children":[{"type":"paragraph","children":[{"type":"text","content":" Configuring React with Webpack seems to be a lot more natural. No need for extra tricks. Tests  with Webpack/React seem to be also easier to configure."}]}]},{"type":"list_item","children":[{"type":"paragraph","children":[{"type":"text","content":" Im starting some experiments with SVG manipulation and Angular directives cause a lot of console errors when rendering SVGs, something related to the template being loaded before being processed. I dont see that kind of errors in React (probably I'll see others, but till now, all looks good)."}]}]}]},{"type":"heading","children":[{"type":"text","content":" What are the steps to do it?"}],"h":"2"},{"type":"bullet_list","children":[{"type":"list_item","children":[{"type":"paragraph","children":[{"type":"text","content":" Configure Webpack and Webpack server. If possible, I want to avoid creating a full Node.js/Express min app, but Im thinking it can be needed"}]}]},{"type":"list_item","children":[{"type":"paragraph","children":[{"type":"text","content":" Recreate all my old directives as React components"}]}]},{"type":"list_item","children":[{"type":"paragraph","children":[{"type":"text","content":" Recreate the logic to load the list of posts and covert it into a Webpack loader"}]}]}]},{"type":"paragraph","children":[{"type":"text","content":" Im thinking that some extra features I can add to this blog can be done as Webpack loaders or npm scripts. Among the things I want to add:"}]},{"type":"bullet_list","children":[{"type":"list_item","children":[{"type":"paragraph","children":[{"type":"text","content":" Tests and tests reports"}]}]},{"type":"list_item","children":[{"type":"paragraph","children":[{"type":"text","content":" Test coverage reports"}]}]},{"type":"list_item","children":[{"type":"paragraph","children":[{"type":"text","content":" Duplicated code reports"}]}]}]},{"type":"paragraph","children":[{"type":"text","content":" Some other nice to have features:"}]},{"type":"bullet_list","children":[{"type":"list_item","children":[{"type":"paragraph","children":[{"type":"text","content":" Sequence diagrams in markdown correctly rendered"}]}]}]},{"type":"heading","children":[{"type":"text","content":" Configure Webpack and Webpack server"}],"h":"3"},{"type":"paragraph","children":[{"type":"text","content":" As I was thinking, at the end I needed to use a node/express app as my develop entry point."}]},{"type":"paragraph","children":[{"type":"text","content":" At my "},{"type":"code_inline","content":"package.json"},{"type":"text","content":"  I have these scripts:"}]},{"type":"code_block","content":"\"scripts\": {\n\t// For when we need to package the app:\n    \"build\": \"webpack -p\",\n\t// Develop mode will run a node app:\n    \"start\": \"node server.js\"\n  }\n"},{"type":"paragraph","children":[{"type":"text","content":" Then, for the "},{"type":"code_inline","content":"server.js"},{"type":"text","content":"  file:"}]},{"type":"code_block","content":"var express = require('express');\nvar path = require('path');\nvar httpProxy = require('http-proxy');\n\nvar proxy = httpProxy.createProxyServer();\nvar app = express();\n\nvar port =  3000;\n\n// We point our static assets to a dummy folder, so that when requesting for /build/* node doesn't use the built\n// files in the real folder.\nvar publicPath = path.resolve(__dirname, 'tmp');\napp.use('/', express.static(publicPath));\n\n// As we are using a dummy folder as our root, we need to set the path for index.html and other files explicitly.\n// This is a bit of an issue, but I'm not expecting to add many static files anyway.\napp.get('/', function(request, response){\n    response.sendFile(path.resolve(__dirname, 'index.html'));\n});\napp.get('/ico32.png', function(request, response){\n    response.sendFile(path.resolve(__dirname, 'ico32.png'));\n});\n\nrequire('./webpack.server.js')();\n\n// Any requests to localhost:3000/build is proxied\n// to webpack-dev-server\napp.all('/build/*', function (req, res) {\n    proxy.web(req, res, {\n        target: 'http://localhost:8080'\n    });\n});\n\n// It is important to catch any errors from the proxy or the\n// server will crash. An example of this is connecting to the\n// server when webpack is bundling\nproxy.on('error', function(e) {\n    console.log('Could not connect to proxy, please try again...');\n});\n\n// And run the server\napp.listen(port, function () {\n    console.log('Node server running at port %s and Webpack Server running at por 8080.', port);\n});\n"},{"type":"paragraph","children":[{"type":"text","content":" We'll need also a manual setup for Webpack as we are not using the default server:"}]},{"type":"code_block","content":"var webpack = require('webpack');\nvar webpackDevServer = require('webpack-dev-server');\nvar webpackConfig = require('./webpack.config.js');\n\nmodule.exports = function () {\n\n    // First we fire up Webpack an pass in the configuration we\n    // created\n    var bundleStart = null;\n    var compiler = webpack(webpackConfig);\n\n    // We give notice in the terminal when it starts bundling and\n    // set the time it started\n    compiler.plugin('compile', function() {\n        console.log('[Webpack] Bundling...');\n        bundleStart = Date.now();\n    });\n\n    // We also give notice when it is done compiling, including the\n    // time it took. Nice to have\n    compiler.plugin('done', function() {\n        console.log('[Webpack] Bundled in ' + (Date.now() - bundleStart) + 'ms!');\n    });\n\n    var bundler = new webpackDevServer(compiler, {\n\n        // We need to tell Webpack to serve our bundled application\n        // from the build path. When proxying:\n        // http://localhost:3000/build -> http://localhost:8080/build\n        publicPath: '/build/',\n\n        // Configure hot replacement\n        hot: true,\n\n        // The rest is terminal configurations\n        quiet: false,\n        noInfo: true,\n        stats: {\n            colors: true\n        }\n    });\n\n    // We fire up the development server and give notice in the terminal\n    // that we are starting the initial bundle\n    bundler.listen(8080, 'localhost', function () {\n        console.log('[Webpack] Bundling project, please wait...');\n    });\n\n};\n"},{"type":"paragraph","children":[{"type":"text","content":" After this, almost everything will work as in any Webpack tutorial."}]},{"type":"heading","children":[{"type":"text","content":" Recreate all my old directives as React components"}],"h":"3"},{"type":"paragraph","children":[{"type":"text","content":" Webpack is running and my app is pretty much broken. I see only a blank page. Now we need to start loading a bunch of things."}]},{"type":"paragraph","children":[{"type":"text","content":" In my old "},{"type":"em","children":[{"type":"text","content":" post"}]},{"type":"text","content":"  directive, I was supporting HTML posts, which I dont really use and can be an issue. Im not supporting them any more."}]},{"type":"paragraph","children":[{"type":"text","content":" Instead of using the jQuery like API to insert the content of my posts after rendering the HTML from the MD files, Im using the method "},{"type":"code_inline","content":"dangerouslySetInnerHTML"},{"type":"text","content":"  from React, that makes me think that I should find a cleaner why to solve this need."}]},{"type":"code_block","content":"// Angular\n// Somehow you get access to the element object.\nvar html = $sanitize(markdown.render(content));\nelement.html(html);\n\n// React\nlet html = { __html: new MD().render(post.content) };\n// In the virtual DOM\n<div className=\"postContent\" dangerouslySetInnerHTML={html}></div>\n"},{"type":"paragraph","children":[{"type":"text","content":" Another important thing is that, because I dont have anymore the "},{"type":"code_inline","content":"$HTTP"},{"type":"text","content":"  object form Angular, now Im relying on the "},{"type":"code_inline","content":"fetch"},{"type":"text","content":"  method and its polyfill."}]},{"type":"paragraph","children":[{"type":"text","content":" This was pretty easy to use in combination with RxJS:"}]},{"type":"code_block","content":"Rx.Observable\n.fromPromise(fetch('build/filelist.json'))\n.flatMap( p => p.json());\n"},{"type":"paragraph","children":[{"type":"text","content":" I moved some of the logic to a Webpack plugin, now Im generating the list of files and extracting the title in this step, instead of doing extra processing in the browser that was also not the cleanest solution."}]}]},"modifiedAt":1457326054000},{"path":"posts/math-js-libraries.md","fileName":"math-js-libraries","title":" Math JavaScript Libraries","date":1433620230000,"content":{"title":" Math JavaScript Libraries","children":[{"type":"heading","children":[{"type":"text","content":" Math JavaScript Libraries"}],"h":"1"},{"type":"paragraph","children":[{"type":"text","content":" Data visualization is very related, if not the same to data manipulation. In most of the cases, data visualization tools will handle all the maths and transformations needed to present your data on the screen, but there are times, many in deed if you are working on a data intensive project, were just sending the data to the charting library as it is coming from the data base or an API is not enough. Of course you can go with your own functions, but before taking that path, or because you are taking it, having other tools as reference can be useful. Here are some."}]},{"type":"heading","children":[{"type":"text","content":" lodash"}],"h":"2"},{"type":"paragraph","children":[{"type":"text","content":" It is probably the best array manipulation library out there. Even if you are writing your own tool, using lodash as a dependency for your library is worth to consider. Even if it is not all about math (it has some handy math methods in the latest versions) it is probably the best swiss knife you can get when handling data sets."}]},{"type":"paragraph","children":[{"type":"text","content":" It was based originally in "},{"type":"link","children":[{"type":"text","content":" Underscore"}],"attrs":{"href":"http://underscorejs.org/"}},{"type":"text","content":", a very known library, but has evolved into a much better replacement for it, at least thats my opinion, you can try both and use whatever you like more."}]},{"type":"paragraph","children":[{"type":"text","content":" In case you are interested in a more serious comparison between both, you can read "},{"type":"link","children":[{"type":"text","content":" Underscore vs Lo-Dash"}],"attrs":{"href":"http://benmccormick.org/2014/11/12/underscore-vs-lodash/"}}]},{"type":"paragraph","children":[{"type":"link","children":[{"type":"text","content":" lodash.com"}],"attrs":{"href":"https://lodash.com/"}}]},{"type":"heading","children":[{"type":"text","content":" Sylvester"}],"h":"2"},{"type":"paragraph","children":[{"type":"text","content":" This one is a linear algebra library dedicated to Vector and Matrix manipulation."}]},{"type":"paragraph","children":[{"type":"link","children":[{"type":"text","content":" sylvester.jcoglan.com"}],"attrs":{"href":"http://sylvester.jcoglan.com/"}}]},{"type":"heading","children":[{"type":"text","content":" jStat"}],"h":"2"},{"type":"paragraph","children":[{"type":"text","content":" Another linear algebra library with a good amount of statistical analysis methods."}]},{"type":"paragraph","children":[{"type":"link","children":[{"type":"text","content":" jstat.github.io"}],"attrs":{"href":"http://jstat.github.io/"}}]}]},"modifiedAt":1433630796000},{"path":"posts/js-complexity-analysis.md","fileName":"js-complexity-analysis","title":" JavaScript Complexity Analysis","date":1432961667000,"content":{"title":" JavaScript Complexity Analysis","children":[{"type":"heading","children":[{"type":"text","content":" JavaScript Complexity Analysis"}],"h":"1"},{"type":"paragraph","children":[{"type":"text","content":" So far I have found two alternatives for doing complexity analysis in JavaScript code."}]},{"type":"paragraph","children":[{"type":"text","content":" The first one is "},{"type":"link","children":[{"type":"text","content":" complexity-report"}],"attrs":{"href":"https://github.com/philbooth/complexity-report"}},{"type":"text","content":"  that uses "},{"type":"link","children":[{"type":"text","content":" escomplex"}],"attrs":{"href":"https://github.com/philbooth/escomplex"}},{"type":"text","content":"  behind the scenes and generates a nice report that can be customized with some CSS."}]},{"type":"paragraph","children":[{"type":"text","content":" The other one is "},{"type":"link","children":[{"type":"text","content":" plato"}],"attrs":{"href":"https://github.com/es-analysis/plato"}},{"type":"text","content":"  that also uses escomplex but that generates a very nice report including code pointers and charts. It is important to note that Plato uses the same options that complexity-report does."}]},{"type":"paragraph","children":[{"type":"text","content":" There are some other tools listed in escomplex GitHub read me file, but the one I tested and liked more is plato."}]},{"type":"paragraph","children":[{"type":"text","content":" As Im already using "},{"type":"link","children":[{"type":"text","content":" Grunt"}],"attrs":{"href":"http://gruntjs.com/"}},{"type":"text","content":"  I installed "},{"type":"link","children":[{"type":"text","content":" grunt-plato"}],"attrs":{"href":"https://github.com/jsoverson/grunt-plato"}},{"type":"text","content":"  that is maintained by the same guys that work on plato (I know that now there is all this debate about getting rid of Grunt or Gulp, and there is also the battle between Grunt and Gulp, but in my case, using Grunt was simpler)."}]},{"type":"paragraph","children":[{"type":"text","content":" The fist step, of course, is install the task tool:"}]},{"type":"code_block","content":"npm install grunt-plato save-dev\n"},{"type":"paragraph","children":[{"type":"text","content":" Then you need to update your "},{"type":"code_inline","content":"Gruntfile.js"},{"type":"text","content":" :"}]},{"type":"code_block","content":"plato: {\n        my_page: {\n            files: { 'plato-report': ['src/**/*.js'] }\n        }\n    }\n"},{"type":"paragraph","children":[{"type":"text","content":" And thats pretty much all, at least with the default configuration."}]},{"type":"paragraph","children":[{"type":"text","content":" The generated report looks like "},{"type":"link","children":[{"type":"text","content":" this one"}],"attrs":{"href":"/plato-report/index.html"}},{"type":"text","content":"  (this is the actual report for my page, this page)."}]}]},"modifiedAt":1432964122000},{"path":"posts/data-visualization-js-libraries.md","fileName":"data-visualization-js-libraries","title":" Data Visualization JavaScript Libraries","date":1432358358000,"content":{"title":" Data Visualization JavaScript Libraries","children":[{"type":"heading","children":[{"type":"text","content":" Data Visualization JavaScript Libraries"}],"h":"1"},{"type":"paragraph","children":[{"type":"text","content":" A good part of my job is related to data visualization in a web application. As probably you know, the most common approach to this problem is to draw the graphical artifacts on the browser, removing load from the servers and giving much more control of the visualization to the front end developers and to the user, as they can now interact with the data. Drawing charts on the browser can be done in only one way, using JavaScript (of course you can use Flash or try Silverlight, but allow me to ignore them during this article), and because doing it all from scratch is not advisable, you will end up using a JavaScript library."}]},{"type":"paragraph","children":[{"type":"text","content":" This is a short list of JavaScript (or Typescript) libraries that I have found in my safaris across the web, some of them I have used, some others I want to try, and there is also the ones I just want to know about. Almost all of them have been open tabs in my browser for months, waiting for me to have some extra time to experiment with them. Today Im closing these tabs and listing them all here with some notes for my self that hopefully can be useful to others too."}]},{"type":"heading","children":[{"type":"text","content":" Highcharts"}],"h":"2"},{"type":"paragraph","children":[{"type":"image","content":"Area Chart","attrs":{"src":"/img/highcharts.jpg","alt":""}}]},{"type":"paragraph","children":[{"type":"text","content":" This one is probably one of the most famous ones. It is commercial but has a pretty good amount of features and if your company can afford the license price, it is probably a good option to bring to the table."}]},{"type":"paragraph","children":[{"type":"text","content":" They offer two versions of their product, the regular one, known simply as Highcharts, that is also the one where more customization can be done, and Highstock, specialized in multi charts and time series."}]},{"type":"paragraph","children":[{"type":"text","content":" They also offer a minimalist version of their charts to be used as a sparklines."}]},{"type":"paragraph","children":[{"type":"link","children":[{"type":"text","content":" highcharts.com"}],"attrs":{"href":"http://www.highcharts.com/"}}]},{"type":"heading","children":[{"type":"text","content":" Plottable"}],"h":"2"},{"type":"paragraph","children":[{"type":"image","content":"Bar Chart","attrs":{"src":"/img/plottable.jpg","alt":""}}]},{"type":"paragraph","children":[{"type":"text","content":" A neat and clean library built on top of "},{"type":"link","children":[{"type":"text","content":" D3"}],"attrs":{"href":"http://d3js.org"}},{"type":"text","content":". It offers the basic charts: bars, lines and scatters. Even if not important for their users, it is nice to know that it is written in "},{"type":"link","children":[{"type":"text","content":" Typescript"}],"attrs":{"href":"http://www.typescriptlang.org/"}},{"type":"text","content":"."}]},{"type":"paragraph","children":[{"type":"link","children":[{"type":"text","content":" plottablejs.org"}],"attrs":{"href":"http://plottablejs.org/"}}]},{"type":"heading","children":[{"type":"text","content":" Chart.js"}],"h":"2"},{"type":"paragraph","children":[{"type":"link","children":[{"type":"text","content":" chart.js"}],"attrs":{"href":"http://www.chartjs.org/"}},{"type":"text","content":" TODO: Add description and immage."}]},{"type":"heading","children":[{"type":"text","content":" Chartist"}],"h":"2"},{"type":"paragraph","children":[{"type":"link","children":[{"type":"text","content":" gionkunz.github.io/chartist-js"}],"attrs":{"href":"http://gionkunz.github.io/chartist-js/"}},{"type":"text","content":" TODO: Add description and immage."}]},{"type":"heading","children":[{"type":"text","content":" React Sparklines"}],"h":"2"},{"type":"paragraph","children":[{"type":"link","children":[{"type":"text","content":" borisyankov.github.io/react-sparklines"}],"attrs":{"href":"http://borisyankov.github.io/react-sparklines/"}},{"type":"text","content":" TODO: Add description and immage."}]},{"type":"heading","children":[{"type":"text","content":" React Paths-js"}],"h":"2"},{"type":"paragraph","children":[{"type":"link","children":[{"type":"text","content":" rsamec.github.io/react-pathjs-chart"}],"attrs":{"href":"http://rsamec.github.io/react-pathjs-chart/"}},{"type":"text","content":" TODO: Add description and immage."}]},{"type":"heading","children":[{"type":"text","content":" Paths.js"}],"h":"2"},{"type":"paragraph","children":[{"type":"link","children":[{"type":"text","content":" andreaferretti.github.io/paths-js-demo"}],"attrs":{"href":"http://andreaferretti.github.io/paths-js-demo/"}},{"type":"text","content":" TODO: Add description and immage."}]},{"type":"heading","children":[{"type":"text","content":" Victory"}],"h":"2"},{"type":"paragraph","children":[{"type":"link","children":[{"type":"text","content":" projects.formidablelabs.com/victory"}],"attrs":{"href":"http://projects.formidablelabs.com/victory/"}},{"type":"text","content":" TODO: Add description and immage."}]},{"type":"heading","children":[{"type":"text","content":" Datakit"}],"h":"2"},{"type":"paragraph","children":[{"type":"image","content":"Line Chart","attrs":{"src":"/img/datakit.jpg","alt":""}}]},{"type":"paragraph","children":[{"type":"text","content":" This one is a much more than a chart library. It also includes some useful features for data analysis as reading data from different sources and statistical methods."}]},{"type":"paragraph","children":[{"type":"link","children":[{"type":"text","content":" github.com/NathanEpstein/datakit"}],"attrs":{"href":"https://github.com/NathanEpstein/datakit"}}]},{"type":"heading","children":[{"type":"text","content":" Envision and Flotr2"}],"h":"2"},{"type":"paragraph","children":[{"type":"image","content":"Area Chart with Zoom","attrs":{"src":"/img/envisionjs.jpg","alt":""}}]},{"type":"paragraph","children":[{"type":"text","content":" Envision is mainly focused on time series, offers simple but well presented charts. It has a nice mode for zooming in and out and for moving the window of visualization across time series (Highstock offers the same feature)."}]},{"type":"paragraph","children":[{"type":"text","content":" It also support very customized charting, as their fractal example shows."}]},{"type":"paragraph","children":[{"type":"link","children":[{"type":"text","content":" www.humblesoftware.com/envision"}],"attrs":{"href":"http://www.humblesoftware.com/envision"}}]},{"type":"paragraph","children":[{"type":"text","content":" Envision is built on the top of Flor2, maintained by the same company and offering an extendable platform for creating charts."}]},{"type":"paragraph","children":[{"type":"link","children":[{"type":"text","content":" www.humblesoftware.com/flotr2"}],"attrs":{"href":"http://www.humblesoftware.com/flotr2/"}}]},{"type":"heading","children":[{"type":"text","content":" JointJS"}],"h":"2"},{"type":"paragraph","children":[{"type":"image","content":"Line Chart","attrs":{"src":"/img/jointjs.jpg","alt":""}}]},{"type":"paragraph","children":[{"type":"text","content":" It covers a very good amount of different needs, from UML to Petri nets, including chess diagrams and organizational charts. It is part of a biggest software that provides a diagramming editor online. The editor has a commercial license, but the library is open source and you can use it for free."}]},{"type":"paragraph","children":[{"type":"link","children":[{"type":"text","content":" jointjs.com"}],"attrs":{"href":"http://jointjs.com/"}}]},{"type":"heading","children":[{"type":"text","content":" dimple"}],"h":"2"},{"type":"paragraph","children":[{"type":"image","content":"Line Chart","attrs":{"src":"/img/dimplejs.jpg","alt":""}}]},{"type":"paragraph","children":[{"type":"text","content":" Works on top of "},{"type":"link","children":[{"type":"text","content":" D3"}],"attrs":{"href":"http://d3js.org"}},{"type":"text","content":"  and claims to provide an easy API for creating charts hiding the complexity of D3."},{"type":"text","content":" The charts styles on this library are one of the cleanest you can get, a ver well implemented graphic design for data visualization."}]},{"type":"paragraph","children":[{"type":"link","children":[{"type":"text","content":" dimplejs.org"}],"attrs":{"href":"http://dimplejs.org/"}}]},{"type":"heading","children":[{"type":"text","content":" MetricsGraphics"}],"h":"2"},{"type":"paragraph","children":[{"type":"image","content":"Area Chart","attrs":{"src":"/img/metricsgraphics.jpg","alt":""}}]},{"type":"paragraph","children":[{"type":"text","content":" Another library on the top of "},{"type":"link","children":[{"type":"text","content":" D3"}],"attrs":{"href":"http://d3js.org"}},{"type":"text","content":"  that also offers neat charts with a minimalistic design. Im not sure I like how it handles the axis, but if you are a fan of Tufte, probably youll like it."}]},{"type":"paragraph","children":[{"type":"link","children":[{"type":"text","content":" metricsgraphicsjs.org"}],"attrs":{"href":"http://metricsgraphicsjs.org/"}}]},{"type":"heading","children":[{"type":"text","content":" nvd3"}],"h":"2"},{"type":"paragraph","children":[{"type":"image","content":"Area Chart","attrs":{"src":"/img/nvd3.jpg","alt":""}}]},{"type":"paragraph","children":[{"type":"text","content":" This one is also built on top of "},{"type":"link","children":[{"type":"text","content":" D3"}],"attrs":{"href":"http://d3js.org"}},{"type":"text","content":". It offers a nice amount of different charts. The generated chart can be sometimes messy, but something you can live with."}]},{"type":"paragraph","children":[{"type":"link","children":[{"type":"text","content":" nvd3.org"}],"attrs":{"href":"http://nvd3.org/"}}]},{"type":"heading","children":[{"type":"text","content":" Flot"}],"h":"2"},{"type":"paragraph","children":[{"type":"image","content":"Combined Chart","attrs":{"src":"/img/flot.jpg","alt":""}}]},{"type":"paragraph","children":[{"type":"text","content":" Created as a jQuery library to support data visualization. Used in some other data intensive tools as "},{"type":"link","children":[{"type":"text","content":" Grafana"}],"attrs":{"href":"http://grafana.org/"}},{"type":"text","content":"."}]},{"type":"paragraph","children":[{"type":"link","children":[{"type":"text","content":" flotcharts.org"}],"attrs":{"href":"http://www.flotcharts.org/"}}]},{"type":"heading","children":[{"type":"text","content":" C3"}],"h":"2"},{"type":"paragraph","children":[{"type":"text","content":" Another tool based on the top of "},{"type":"link","children":[{"type":"text","content":" D3"}],"attrs":{"href":"http://d3js.org"}},{"type":"text","content":". I has a quite friendly API and a nice presentation; mainly useful if you are adding or removing data dynamically from your charts. Not a huge amount of flexibility on the presentation, but if you are more focused on dynamic loading/unloading and dont want to do a lot of coding, this tool is quite efficient."}]},{"type":"paragraph","children":[{"type":"link","children":[{"type":"text","content":" c3js.org"}],"attrs":{"href":"http://c3js.org/"}}]},{"type":"heading","children":[{"type":"text","content":" Project EON"}],"h":"2"},{"type":"paragraph","children":[{"type":"text","content":" This tool is build on the top of "},{"type":"link","children":[{"type":"text","content":" C3"}],"attrs":{"href":"http://c3js.org/"}},{"type":"text","content":", and can be seen also as part of the "},{"type":"link","children":[{"type":"text","content":" D3"}],"attrs":{"href":"http://d3js.org"}},{"type":"text","content":"  family. It offers a nice API (inherited from the also simple API from C3) for handling dynamic data for real time presentations."}]},{"type":"paragraph","children":[{"type":"em","children":[{"type":"text","content":" Unrelated note: Its web site is a bit overloaded and it is not recomendable to leave it there as an open tab for centuries unless you want to find it dead as it happened to me."}]}]},{"type":"paragraph","children":[{"type":"link","children":[{"type":"text","content":" pubnub.com/developers/eon"}],"attrs":{"href":"http://www.pubnub.com/developers/eon/"}}]},{"type":"heading","children":[{"type":"text","content":" d3-timeline"}],"h":"2"},{"type":"paragraph","children":[{"type":"link","children":[{"type":"text","content":" github.com/jiahuang/d3-timeline"}],"attrs":{"href":"https://github.com/jiahuang/d3-timeline"}},{"type":"text","content":" TODO: Add description and immage."}]},{"type":"heading","children":[{"type":"text","content":" Why not D3"}],"h":"2"},{"type":"paragraph","children":[{"type":"text","content":" There is nothing wrong with "},{"type":"link","children":[{"type":"text","content":" D3"}],"attrs":{"href":"http://d3js.org"}},{"type":"text","content":", actually, it is probably the most complete set of tools for creating visualizations with JavaScript. But in my personal opinion, it is  very generic and it is far from being a domain specific solution."}]},{"type":"paragraph","children":[{"type":"text","content":" You can create a domain specific solution on the top of D3, and many libraries are doing that, but using it without any helping library can become a hard task."}]},{"type":"paragraph","children":[{"type":"text","content":" When you are visualizing data, you dont want to invest tons of hours creating charts, you want to work with your data and analyze it with the help of your visualizations; that is why I believe that using a chart specific tool is a better idea than playing directly with D3 or "},{"type":"link","children":[{"type":"text","content":" Rafael"}],"attrs":{"href":"http://raphaeljs.com/"}},{"type":"text","content":", as well as playing these tools is huge better than drawing yourself on a canvas element."}]},{"type":"paragraph","children":[{"type":"text","content":" On the other hand, if you are doing your own visualization library, or you want a super customized visualization, looking into a generic tool like D3 can be a wise idea."}]},{"type":"heading","children":[{"type":"text","content":" Some other folks talking about the same things:"}],"h":"2"},{"type":"paragraph","children":[{"type":"text","content":" Well, it is not just that they also talk about the same things, but that they are experts on the field (which I am not)."}]},{"type":"paragraph","children":[{"type":"link","children":[{"type":"text","content":" Edward Tufte"}],"attrs":{"href":"http://www.edwardtufte.com/tufte/index"}},{"type":"text","content":"  is an essential reference on the data visualization world. If you dont know him, try to google his name and look around of what people says about him and of course, what he himself have and is saying. In my opinion, his "},{"type":"em","children":[{"type":"text","content":" data ink ratio"}]},{"type":"text","content":"  and the "},{"type":"em","children":[{"type":"text","content":" sparklines"}]},{"type":"text","content":"  are two of the most interesting ideas in data visualization. His most known work is "},{"type":"link","children":[{"type":"text","content":" The Visual Display of Quantitative Information"}],"attrs":{"href":"http://www.amazon.com/gp/product/0961392142/ref=as_li_tl?ie=UTF8&camp=1789&creative=390957&creativeASIN=0961392142&linkCode=as2&tag=wigahlukblog-20&linkId=6M4GTOWCQXDBDM4U"}},{"type":"text","content":"."}]},{"type":"paragraph","children":[{"type":"link","children":[{"type":"text","content":" The Functional Art: An introduction to information graphics and visualization"}],"attrs":{"href":"http://www.amazon.com/gp/product/0321834739/ref=as_li_tl?ie=UTF8&camp=1789&creative=390957&creativeASIN=0321834739&linkCode=as2&tag=wigahlukblog-20&linkId=SSXTTBZHZB3JFAWH"}},{"type":"text","content":"  is a must read if you are in the data visualization world. Available in "},{"type":"link","children":[{"type":"text","content":" Safari"}],"attrs":{"href":"my.safaribooksonline.com"}},{"type":"text","content":"."}]},{"type":"paragraph","children":[{"type":"link","children":[{"type":"text","content":" Jim Vallandingham"}],"attrs":{"href":"http://vallandingham.me/"}},{"type":"text","content":"  has a very interesting blog about visualization and data processing."}]},{"type":"paragraph","children":[{"type":"link","children":[{"type":"text","content":" The Why Axis"}],"attrs":{"href":"http://thewhyaxis.info/"}},{"type":"text","content":"  is a blog from "},{"type":"link","children":[{"type":"text","content":" Bryan Connor"}],"attrs":{"href":"http://www.bryanconnor.com/"}},{"type":"text","content":"  dedicated to data visualization."}]}]},"modifiedAt":1449461570000},{"path":"posts/autogenerate-the-index-of-posts.md","fileName":"autogenerate-the-index-of-posts","title":" Autogenerate the Index of Posts","date":1431905194000,"content":{"title":" Autogenerate the Index of Posts","children":[{"type":"heading","children":[{"type":"text","content":" Autogenerate the Index of Posts"}],"h":"1"},{"type":"paragraph","children":[{"type":"text","content":" The next feature I want to add is actually not a change on the usability of this site, but on my own usability."},{"type":"text","content":" Writing manually the list of entries each time I add something is not very fun. What I want is to write the articles and"},{"type":"text","content":" then the machine to generate the index of entries for me."}]},{"type":"paragraph","children":[{"type":"text","content":" These are the things I need to do:"}]},{"type":"bullet_list","children":[{"type":"list_item","children":[{"type":"paragraph","children":[{"type":"text","content":" Read the files in a certain directory"}]}]},{"type":"list_item","children":[{"type":"paragraph","children":[{"type":"text","content":" Generate a JSON file with some metadata for each file: Creation date, title, path."}]}]},{"type":"list_item","children":[{"type":"paragraph","children":[{"type":"text","content":" Update my post service to use the generated index file."}]}]},{"type":"list_item","children":[{"type":"paragraph","children":[{"type":"text","content":" Update everything else to use this new capability."}]}]}]},{"type":"paragraph","children":[{"type":"text","content":" Done! :)"}]},{"type":"paragraph","children":[{"type":"text","content":" I started with npm alone, but after struggling a bit with it and the minification and generation of files, I ended up"},{"type":"text","content":" configuring Grunt for the project."}]},{"type":"paragraph","children":[{"type":"text","content":" Now I'll start adding all my files to the minification list. But for now, Im good."}]}]},"modifiedAt":1431928067000},{"path":"posts/creating-my-own-showdown-directive.md","fileName":"creating-my-own-showdown-directive","title":" Creating my own Showdown directive","date":1431669834000,"content":{"title":" Creating my own Showdown directive","children":[{"type":"heading","children":[{"type":"text","content":" Creating my own Showdown directive"}],"h":"1"},{"type":"paragraph","children":[{"type":"text","content":" I was using, as I mentioned earlier, the "},{"type":"em","children":[{"type":"text","content":" btford"}]},{"type":"text","content":"  angular-markdown-directive and it was working pretty nice, until I tried to include some HTML code in my markdown files. Looking into the directives code, I noticed that it was using "},{"type":"code_inline","content":"ng-include"},{"type":"text","content":"  as the mechanism to retrieve the markdown files."}]},{"type":"paragraph","children":[{"type":"text","content":" When Angular 1.x loads the content using "},{"type":"code_inline","content":"ng-include"},{"type":"text","content":", it is applying what they call"},{"type":"link","children":[{"type":"text","content":" \"Strict Contextual Escaping\""}],"attrs":{"href":"https://docs.angularjs.org/api/ng/service/$sce"}},{"type":"text","content":"  and it was escaping my HTML tags."}]},{"type":"paragraph","children":[{"type":"text","content":" As there was not an easy solution that prevented this escaping, I decided to create my own directive and use the "},{"type":"code_inline","content":"$http"},{"type":"text","content":"  module for loading my files."}]},{"type":"paragraph","children":[{"type":"text","content":" I have a problem focusing on a single thing and it is pretty common that I get distracted in the middle of my way."},{"type":"text","content":" Thats what happened this time. Once I started planning for my new directive I started thinking on what were the features"},{"type":"text","content":" I would like to have on it:"}]},{"type":"bullet_list","children":[{"type":"list_item","children":[{"type":"paragraph","children":[{"type":"text","content":" Being able to define the content through a directive parameter. This was my main use case as I was also building my own service for handling my articles. I know I said I wanted a very simple thing at the beginning, but adding dates on each HTML entry was becoming difficult for a distracted person like me, even with a couple of articles :P"}]}]},{"type":"list_item","children":[{"type":"paragraph","children":[{"type":"text","content":" Support urls for and download the remote content. Supposing that the content is coming from a trusted source that support "},{"type":"code_inline","content":"CORS"},{"type":"text","content":"."}]}]},{"type":"list_item","children":[{"type":"paragraph","children":[{"type":"text","content":" Support embedded content into the same directive, something similar to this: "},{"type":"code_inline","content":"<ng-showdown>#My Title</ng-showdown>"}]}]}]},{"type":"paragraph","children":[{"type":"text","content":" So, after defining my requirements, more than what I really needed in fact, I started coding my directive. The one that now Im using here :)"}]},{"type":"paragraph","children":[{"type":"text","content":" At the end, I was able to support the following cases:"}]},{"type":"code_block","content":"<ng-showdown>Inserting markdown in the HTML</ng-showdown>\n<ng-showdown source=\"myScopeVariable\"></ng-showdown>\n<ng-showdown href=\"myurl.md\"></ng-showdown>\n"},{"type":"paragraph","children":[{"type":"text","content":" The last thing I needed was to be able to add some extra post processing before any rendering."},{"type":"text","content":" I added an extra parameter called "},{"type":"code_inline","content":"onLoad"},{"type":"text","content":"  that expects a function like:"}]},{"type":"code_block","content":"function myHandler (data, xhr) {}\n"},{"type":"paragraph","children":[{"type":"text","content":" Where "},{"type":"code_inline","content":"data"},{"type":"text","content":"  is the content of the file and "},{"type":"code_inline","content":"xhr"},{"type":"text","content":"  is the "},{"type":"em","children":[{"type":"text","content":" xhr"}]},{"type":"text","content":"  object from the "},{"type":"em","children":[{"type":"text","content":" jQuery"}]},{"type":"text","content":"  Ajax call."}]},{"type":"paragraph","children":[{"type":"text","content":" As an extra feature, I changed a bit the styling of the site. It still needs some refactoring though, but now it doesnt looks as old fashion as before :)"}]},{"type":"heading","children":[{"type":"text","content":" Update (06/06/15):"}],"h":"2"},{"type":"paragraph","children":[{"type":"text","content":" After some issues with Showdown as my rendering engine for markdown I decided to look for some other tools. This is what I found:"}]},{"type":"heading","children":[{"type":"link","children":[{"type":"text","content":" Showdown"}],"attrs":{"href":"https://github.com/showdownjs/showdown"}}],"h":"3"},{"type":"paragraph","children":[{"type":"text","content":" This was my original engine, and it is actually pretty nice to use for some basic needs, but it does the rendering applying a chain of global replacements using regular expressions that use the full text every time. If you are interested in adding some extensions, it can cause an issue on performance, besides the fact that if you are not careful, you can modify the output of another transformation."}]},{"type":"heading","children":[{"type":"link","children":[{"type":"text","content":" markded"}],"attrs":{"href":"https://github.com/chjj/marked"}}],"h":"3"},{"type":"paragraph","children":[{"type":"text","content":" This is probably the fastest markdown tool you will find for JavaScript, but because it is so optimized, it doesnt allow extensions and customization seems to need a complete fork of the code."}]},{"type":"heading","children":[{"type":"link","children":[{"type":"text","content":" Markdown-it"}],"attrs":{"href":"https://github.com/markdown-it/markdown-it"}}],"h":"3"},{"type":"paragraph","children":[{"type":"text","content":" This one is my new choice. It is not as fast as markded but it does a pretty decent job. It is also easy to extend and has a good amount of options to customize the rendering. The same as marked, it doesnt apply brute force on the replacements, instead, it creates a tree of tokens that afterwards processed in a render. Accordingly to their authors, it is derived from Remarkable."}]},{"type":"heading","children":[{"type":"link","children":[{"type":"text","content":" Remarkable"}],"attrs":{"href":"https://github.com/jonschlinkert/remarkable"}}],"h":"3"},{"type":"paragraph","children":[{"type":"text","content":" It is one of the most mentioned parsers out there. I havent tried it, but many tutorials have a reference to it. They have a nice demo of the functionality and seems pretty well documented. This library seems to have a good number of plugins, it is also parsing and creating a syntax tree, as Markdown-it. (Added to this list on 12/01/15)"}]},{"type":"heading","children":[{"type":"link","children":[{"type":"text","content":" markdown-js"}],"attrs":{"href":"https://github.com/evilstreak/markdown-js"}}],"h":"3"},{"type":"paragraph","children":[{"type":"text","content":" Another nice tool with a three steps process. Tokenize and parse into  (JsonML)[http://www.jsonml.org/] , convert to an HTML tree and generate the HTML string. It was my second choice. I decided not to use it because Markdown-it had much more extensions already done."}]},{"type":"heading","children":[{"type":"link","children":[{"type":"text","content":" text.js"}],"attrs":{"href":"https://github.com/sheremetyev/texts.js"}}],"h":"3"},{"type":"paragraph","children":[{"type":"text","content":" Another option, if you are interested in a more generic solution that can be used to render HTML, XeLaTex and Pandoc, using a very similar format to (JsonML)[http://www.jsonml.org/] as its internal representation. It doesnt support the full Markdown specification, but the subset it does is enough for almost all cases."}]},{"type":"heading","children":[{"type":"link","children":[{"type":"text","content":" pdfmake"}],"attrs":{"href":"http://pdfmake.org/"}}],"h":"3"},{"type":"paragraph","children":[{"type":"text","content":" This is not a Markdown tool, but a PDF render with basic functionality that can be combined with any of the tools that generate intermediate formats as (JsonML)[http://www.jsonml.org/]. Im adding it to the list because I dont have one for PDF generators."}]}]},"modifiedAt":1449461570000},{"path":"posts/adding-support-for-reading-a-single-file.md","fileName":"adding-support-for-reading-a-single-file","title":" Adding support for reading a single file","date":1431062500000,"content":{"title":" Adding support for reading a single file","children":[{"type":"heading","children":[{"type":"text","content":" Adding support for reading a single file"}],"h":"1"},{"type":"paragraph","children":[{"type":"text","content":" I know this sounds a bit trivial, and in fact it is, all blogs I know support single file visualization. Why this small"},{"type":"text","content":" app not too?"}]},{"type":"paragraph","children":[{"type":"text","content":" The first thing I need to confess is that I started this as an exercise to teach someone how an angular app looks like,"},{"type":"text","content":" and so I wanted a very small and limited version. This is my forth post here, and I think it is time now to build a very"},{"type":"text","content":" basic functionality to support this very basic feature."}]},{"type":"paragraph","children":[{"type":"text","content":" My posts at this point in time are just MD files. I have a controller with tags and each tag has a reference to the file"},{"type":"text","content":" it should render. It looks like this:"}]},{"type":"code_block","content":"<post timestamp=\"12/21/2014\" file=\"implicit-read-write-objects-play-2.3\"></post>\n"},{"type":"paragraph","children":[{"type":"text","content":" I know it is pretty basic, and probably more basic than that, but as I said, this was more a demo than a real blog,"},{"type":"text","content":" and it is still just a nice demo, no more than that so far."}]},{"type":"paragraph","children":[{"type":"text","content":" The first problem I have is not really related to this task, but to the markdown directive Im using that depends on Angular"},{"type":"text","content":" ng-include to load files. This is not ideal as it is sanitizing the HTML code Im adding to this post. So, lets do a short"},{"type":"text","content":" pause to fix that building my own directive for using Showdown."}]},{"type":"paragraph","children":[{"type":"text","content":" Done. After this small debrief, lets continue with the implementation for single post viewer."}]},{"type":"paragraph","children":[{"type":"text","content":" The first thing: Add an extra controller for posts and use the post directive I already have with the modified Showdown"},{"type":"text","content":" directive."}]},{"type":"paragraph","children":[{"type":"text","content":" The next step was just adding an extra path to the "},{"type":"em","children":[{"type":"text","content":" $routeProvider"}]},{"type":"text","content":"."}]},{"type":"paragraph","children":[{"type":"text","content":" Now I need to use this new feature. The simplest way is just to add a link to the title of each article. Nothing fancy right?"}]},{"type":"paragraph","children":[{"type":"text","content":" Well, the main problem is that the HTML generated by Showdown is not part of the angular process, so I need to find the"},{"type":"text","content":" first H1 element and wrap it with the anchor with the old jquery style programming :("}]},{"type":"paragraph","children":[{"type":"text","content":" Done! Now I have a single post view that works with a nice URL. I dont like the implementation too much, but lets think"},{"type":"text","content":" of it as a first version :)"}]}]},"modifiedAt":1431062724000},{"path":"posts/implicit-read-write-objects-play-2.3.md","fileName":"implicit-read-write-objects-play-2.3","title":" Implicit read and write objects for JSON in Play 2.3","date":1419208490000,"content":{"title":" Implicit read and write objects for JSON in Play 2.3","children":[{"type":"heading","children":[{"type":"text","content":" Implicit read and write objects for JSON in Play 2.3"}],"h":"1"},{"type":"paragraph","children":[{"type":"text","content":" Last week I was trying to have a prototype working in Scala/Play 2.3 framework. All was quite nice and my first steps on"},{"type":"text","content":" Scala were not so difficult until I tried to use the "},{"type":"link","children":[{"type":"text","content":" JSON Macro Inception"}],"attrs":{"href":"https://www.playframework.com/documentation/2.3.x/ScalaJsonInception"}},{"type":"text","content":" functionality."}]},{"type":"paragraph","children":[{"type":"text","content":" The documentation was clear enough for the simple examples they had, but I was unable to make it work on my code. My example is not complicated"},{"type":"text","content":" either:"}]},{"type":"code_block","content":"case class TimeSeriesData (Response: Response)\ncase class Response(TimeUnit: List[Long], stats: Stats)\ncase class Stats(data: List[Data])\ncase class Data(identifier: Identifier, metric: List[Metric])\ncase class Identifier(names: List[String], values: List[String])\ncase class Metric(env: String, name: String, values: List[Double])\n"},{"type":"paragraph","children":[{"type":"text","content":" but I was getting an error all the time:"}]},{"type":"code_block","content":"play.PlayExceptions$CompilationException: Compilation error[No implicit Reads for assets.{some_class} available.]\n"},{"type":"paragraph","children":[{"type":"text","content":" After looking into a lot of places and trying all kind of superstitious ideas (as often I start having after some time of"},{"type":"text","content":" dealing with errors I dont quite understand) I finally realized that the problem was the order in which you declare your implicit objects."}]},{"type":"paragraph","children":[{"type":"strong","children":[{"type":"text","content":" Every implicit object declared should not be used in any previous declaration but can be used in any further one."}]}]},{"type":"code_block","content":"implicit val metricReads = Json.reads[Metric]\nimplicit val identifierReads = Json.reads[Identifier]\nimplicit val dataReads = Json.reads[Data]\nimplicit val statsReads = Json.reads[Stats]\nimplicit val responseReads = Json.reads[Response]\nimplicit val timeSeriesDataReads = Json.reads[TimeSeriesData]\n"},{"type":"paragraph","children":[{"type":"text","content":" And thats all! The implicit objects with the JSON Macro Inception works awesomely!"}]}]},"modifiedAt":1419208490000},{"path":"posts/adding-google-ax.md","fileName":"adding-google-ax","title":" Adding Google Analytics","date":1418535602000,"content":{"title":" Adding Google Analytics","children":[{"type":"heading","children":[{"type":"text","content":" Adding Google Analytics"}],"h":"1"},{"type":"paragraph","children":[{"type":"text","content":" I decided to start a new post but continue writing about the process of creating this blog-like thing because now I'll"},{"type":"text","content":" integrate some other services that are not completely related to the code and infrastructure I need for this application"},{"type":"text","content":" but rather are more like accessories."}]},{"type":"paragraph","children":[{"type":"text","content":" I already had an account for google analytics, so the natural next step was to create a new property."}]},{"type":"paragraph","children":[{"type":"text","content":" The next step: embed the javascript code for this to work."}]},{"type":"paragraph","children":[{"type":"text","content":" Finally, because this app is an "},{"type":"em","children":[{"type":"text","content":" angular"}]},{"type":"text","content":"  app, I needed a way to track page interactions for a it. Im using"},{"type":"link","children":[{"type":"text","content":" angulartics"}],"attrs":{"href":"http://luisfarzati.github.io/angulartics/"}},{"type":"text","content":". For now, I'll keep the very basic configuration."}]}]},"modifiedAt":1418535602000},{"path":"posts/setting-up-this-thing.md","fileName":"setting-up-this-thing","title":" Setting up this thing","date":1418531517000,"content":{"title":" Setting up this thing","children":[{"type":"heading","children":[{"type":"text","content":" Setting up this thing"}],"h":"1"},{"type":"paragraph","children":[{"type":"text","content":" Im starting this... thing. Ive started some other blogs, personal pages, etc. in the past. Fore some reason Ive never"},{"type":"text","content":" maintained them in a regular way."}]},{"type":"paragraph","children":[{"type":"text","content":" I think probably my old blog at "},{"type":"em","children":[{"type":"text","content":" Wordpress"}]},{"type":"text","content":"  was the one where I wrote more on but then, once I saw it"},{"type":"text","content":" without being signed in and saw a lot of advertisements (not really a lot, but more than what I liked) I lost the interest on it."}]},{"type":"paragraph","children":[{"type":"text","content":" Then I tried "},{"type":"em","children":[{"type":"text","content":" tumblr"}]},{"type":"text","content":", "},{"type":"em","children":[{"type":"text","content":" blogger"}]},{"type":"text","content":", "},{"type":"em","children":[{"type":"text","content":" twitter"}]},{"type":"text","content":"  (I know its not the same) and for some reason I dont get really involved with them."}]},{"type":"paragraph","children":[{"type":"text","content":" There are no guaranties that this quasi blog at github will have a better luck, we'll see. There are some things I like so far:"}]},{"type":"bullet_list","children":[{"type":"list_item","children":[{"type":"paragraph","children":[{"type":"text","content":" I can write code directly"}]}]},{"type":"list_item","children":[{"type":"paragraph","children":[{"type":"text","content":" I can have a source control on it"}]}]},{"type":"list_item","children":[{"type":"paragraph","children":[{"type":"text","content":" I can have branches"}]}]},{"type":"list_item","children":[{"type":"paragraph","children":[{"type":"text","content":" I can iterate on it"}]}]}]},{"type":"paragraph","children":[{"type":"text","content":" So this is pretty much as my first post here. I want to add some more stuff, mainly, I want to be able to write my posts"},{"type":"text","content":" using markdown instead of pure HTML, and also have some nice styles for them including something for dates (this post has no timestamp"},{"type":"text","content":" nor any other fancy metadata :( )."}]},{"type":"paragraph","children":[{"type":"text","content":" Just as a quick note, right now, this blog-like is done as an "},{"type":"em","children":[{"type":"link","children":[{"type":"text","content":" angular"}],"attrs":{"href":"https://angularjs.org/"}}]},{"type":"text","content":"  application deployed as a"},{"type":"link","children":[{"type":"text","content":" gitHub page"}],"attrs":{"href":"https://pages.github.com/"}},{"type":"text","content":". Im using "},{"type":"link","children":[{"type":"text","content":" Bootstrap"}],"attrs":{"href":"http://getbootstrap.com/"}},{"type":"text","content":"  for my basic styles, "},{"type":"link","children":[{"type":"text","content":" google fonts"}],"attrs":{"href":"http://www.google.com/fonts"}},{"type":"text","content":"  and"},{"type":"link","children":[{"type":"text","content":" philbit svg patterns"}],"attrs":{"href":"http://philbit.com/svgpatterns/"}},{"type":"text","content":"  for the background images."}]},{"type":"paragraph","children":[{"type":"text","content":" Lets try to add some MD support for this rudimentary blog-like thing."}]},{"type":"paragraph","children":[{"type":"text","content":" First TODO, find a library. Done: "},{"type":"em","children":[{"type":"link","children":[{"type":"text","content":" angular-markdown-directive"}],"attrs":{"href":"https://github.com/btford/angular-markdown-directive"}}]},{"type":"text","content":". This one works perfect for what I want to do."}]},{"type":"paragraph","children":[{"type":"text","content":" Now, replace the about page with the "},{"type":"code_inline","content":"README.md"},{"type":"text","content":"  content. Done."}]},{"type":"paragraph","children":[{"type":"text","content":" Now, lets suppose there are a lot of articles, well, not a lot, but some of them, and that they have the basic metadata, as a timestamp."}]},{"type":"paragraph","children":[{"type":"text","content":" I did some styling out of the plan. Changing fonts here and there, now lets back to the plan. Convert this text into an MD file. Done."}]},{"type":"paragraph","children":[{"type":"text","content":" Lets create a very simple directive for posts (BTW it is nicer to write texts in MD format than in HTML). Done."}]},{"type":"paragraph","children":[{"type":"text","content":" Im not sure I want to support comments and all these fancy features a common blog has. For now, this blog is pretty unknown and is more an exercise and a toy than a real"},{"type":"text","content":" publishing platform."}]},{"type":"paragraph","children":[{"type":"text","content":" Next thing was to create a nicer directive for book recommendations. Thats done. I need to write some of them now :)."}]}]},"modifiedAt":1419204782000}]};

